import React from 'react'
import {PancakeRoundIcon, Card, CardBody, Heading, Text } from '@pancakeswap-libs/uikit'
import BigNumber from 'bignumber.js/bignumber'
import styled from 'styled-components'
import { getBalanceNumber } from 'utils/formatBalance'
import { useTotalSupply, useBurnedBalance } from 'hooks/useTokenBalance'
import useI18n from 'hooks/useI18n'
import { getCakeAddress } from 'utils/addressHelpers'
import CardValue from './CardValue'
import { useFarms, usePriceCakeBusd } from '../../../state/hooks'

const StyledCakeStats = styled(Card)`
  margin-left: auto;
  margin-right: auto;
`

const Row = styled.div`
  align-items: center;
  display: flex;
  font-size: 14px;
  justify-content: space-between;
  margin-bottom: 8px;
`

const CakeStats = () => {
  const TranslateString = useI18n()
  const totalSupply = useTotalSupply()
  const burnedBalance = useBurnedBalance(getCakeAddress())
  const farms = useFarms();
  const eggPrice = usePriceCakeBusd();
  const circSupply = totalSupply ? totalSupply.minus(burnedBalance) : new BigNumber(0);
  const cakeSupply = getBalanceNumber(circSupply);
  const marketCap = eggPrice.times(circSupply);

  let tresPerBlock = 0;
  if(farms && farms[0] && farms[0].tresPerBlock){
    tresPerBlock = new BigNumber(farms[0].tresPerBlock).div(new BigNumber(10).pow(18)).toNumber();
  }

  return (
    <StyledCakeStats>
      <CardBody>
        <Row>
          <PancakeRoundIcon width="24px" mr="8px" />
          {cakeSupply && <CardValue fontSize="14px" value={Number(eggPrice)} decimals={0} prefix="$"/>}
        </Row>
        <Row>
          <Text fontSize="14px">{TranslateString(999, 'Total Supply')}</Text>
          {cakeSupply && <CardValue fontSize="14px" value={cakeSupply} decimals={0} />}
        </Row>
        <Row>
          <Text fontSize="14px">{TranslateString(999, 'Market Cap')}</Text>
          <CardValue fontSize="14px" value={getBalanceNumber(marketCap)} decimals={0} prefix="$" />
        </Row>
        <Row>
          <Text fontSize="14px">{TranslateString(538, 'Total TRES Burned')}</Text>
          <CardValue fontSize="14px" value={getBalanceNumber(burnedBalance)} decimals={0} />
        </Row>
        <Row>
          <Text fontSize="14px">{TranslateString(540, 'New TRES/block')}</Text>
          <Text bold fontSize="14px">{tresPerBlock}</Text>
        </Row>
        <Row>
          <Text fontSize="14px">{TranslateString(10004, 'Total TREE Existing')}</Text>
          <Text bold fontSize="14px">0</Text>
        </Row>
        <Row>
          <Text fontSize="14px">{TranslateString(10005, 'Total TREE Planted')}</Text>
          <Text bold fontSize="14px">1</Text>
        </Row>
        <Row>
          <Text fontSize="14px">{TranslateString(10006, 'TREE Wave')}</Text>
          <Text bold fontSize="14px">1</Text>
        </Row>
      </CardBody>
    </StyledCakeStats>
  )
}

export default CakeStats
